@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes glow {
  0%, 100% { box-shadow: 0 0 5px currentColor; }
  50% { box-shadow: 0 0 20px currentColor, 0 0 30px currentColor; }
}

@keyframes spin-slow {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

.animate-glow {
  animation: glow 2s ease-in-out infinite;
}

.animate-spin-slow {
  animation: spin-slow 8s linear infinite;
}

.animate-gradient {
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}

.pollution-particle {
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
  animation: float 4s ease-in-out infinite;
}

.pollution-particle:nth-child(1) { animation-delay: 0s; }
.pollution-particle:nth-child(2) { animation-delay: 0.5s; }
.pollution-particle:nth-child(3) { animation-delay: 1s; }
.pollution-particle:nth-child(4) { animation-delay: 1.5s; }
.pollution-particle:nth-child(5) { animation-delay: 2s; }
